.search-form {
        background-color: #fff;
        margin: 0 auto;
        max-width: vw(954);
        display: flex;
        padding: vw(13) vw(15);
        border-radius: vw(10);
        .mfp-close {
                display: none;
        }
        ::placeholder {
                color: #A0B1C1;
        }
        &__btn {
                font-size: vw(14);
                height: vw(68);
                width: vw(152);
                border: none;
                border-radius: vw(10);
                margin-left: vw(7);
                color: #fff;
                background-color: $color-indigo;
                &:hover {
                        background-color: darken($color-indigo, 10%);
                }
                @include desktop-xl {
                        font-size: px(14);
                        height: px(68);
                        width: px(152);
                        border-radius: px(10);
                        margin-left: px(7);
                }
                @include devices {
                        font-size: vm(14);
                        height: vm(50);
                        width: auto;
                        flex-shrink: 0;
                        border-radius: vm(10);
                        margin-left: vm(7);
                        padding-left: vm(16);
                        padding-right: vm(16);
                        svg {
                                width: vm(24);
                                height: vm(24)
                        }
                }
        }
        &__input {
                flex: 1 1;
                border: none;
                height: vw(68);
                text-indent: vw(45);
                border-radius: vw(10);
                font-size: vw(15);
                font-weight: 500;
                border: 1px solid #F0F4F8;
                background: #F0F4F8 url(../images/loupe.svg) no-repeat left vw(15) center;
                background-size: vw(24);
                transition: all .3s ease-out;
                &:focus {
                        border: 1px solid $color-indigo;
                }
                @include desktop-xl {
                        height: px(68);
                        text-indent: px(45);
                        border-radius: px(10);
                        font-size: px(15);
                        background-size: px(24);
                }
                @include devices {
                        height: vm(50);
                        text-indent: vm(15);
                        border-radius: vm(10);
                        font-size: vm(13);
                        background-size: vm(24);
                        background-image: none;
                }
        }
        @include desktop-xl {
                max-width: px(954);
                padding: px(13) px(15);
                border-radius: px(10);      
        }
        @include devices {
                max-width: vm(270);
                padding: vm(10) vm(10);
                border-radius: vm(10);          
        }
}