.crumbs {
        font-size: vw(13);
        padding: vw(10) 0;
        background-color: #F0F4F6;
        &__wrapper {
                display: flex;
                align-items: center;
                svg {
                        width: vw(24);
                        height: vw(24);
                        margin-right: vw(10);
                        @include desktop-xl {
                                width: px(24);
                                height: px(24);
                                margin-right: px(10);
                        }
                        @include devices { 
                                width: vm(24);
                                height: vm(24);
                                margin-right: vm(10);
                        }
                }
        }
        ol {
                display: flex;
                padding-left: 0;
                flex-wrap: wrap;
                li {
                        margin-right: vw(5);
                        &:after {
                                content: " / ";
                        }
                        &:last-child {
                                &:after {
                                        display: none;
                                }
                        }
                        a {
                                &:hover {
                                        color: $color-brown-light;
                                }
                        }
                        @include desktop-xl {
                                margin-right: px(5);
                        }
                        @include devices { 
                                margin-right: vm(5);
                        }
                }
        }
        @include desktop-xl {
                font-size: px(13);
                padding: px(10) 0;                  
        }
        @include devices { 
                font-size: vm(11);
                padding: vm(10) 0;            
        }
}