.aside-menu {
        position: sticky;
        top: vw(15);
        font-size: vw(18);
        background-color: #FFF0E0;
        margin-bottom: vw(45);
        &--nosticky {
                position: relative;
                top: auto !important;
        }
        ul {
                li {
                        a {
                                display: flex;
                                padding: vw(16);
                                padding-left: vw(40);
                                &:hover {
                                        background: #FAD1A6
                                }
                                @include desktop-xl {
                                        padding: px(16);
                                        padding-left: px(40);
                                }
                                @include devices { 
                                        padding: vm(16);
                                        padding-left: vm(20);
                                }
                        }
                        span {
                                display: flex;
                                padding: vw(16);
                                padding-left: vw(40);
                                padding-right: vw(40);
                                @include desktop-xl {
                                        padding: px(16);
                                        padding-left: px(40);
                                        padding-right: px(40);
                                }
                                @include devices { 
                                        padding: vm(16);
                                        padding-left: vm(20);
                                        padding-right: vm(40);
                                }
                        }
                        &.is-active {
                                span {
                                        background: #FAD1A6 url(../images/sprites/svg/arrow-menu.svg) no-repeat right vw(15) center;
                                        background-size: vw(24);
                                        @include desktop-xl {
                                                background-size: px(24);
                                                background: #FAD1A6 url(../images/sprites/svg/arrow-menu.svg) no-repeat right px(15) center;
                                        }
                                        @include devices { 
                                                background-size: vm(24);
                                                background: #FAD1A6 url(../images/sprites/svg/arrow-menu.svg) no-repeat right vm(15) center;
                                        }
                                }
                        }
                }
        }
        @include desktop-xl {
                top: px(15);
                font-size: px(18);      
                margin-bottom: px(45);              
        }
        @include devices { 
                position: relative;
                top: 0;
                font-size: vm(15);      
                margin-bottom: vm(45);     
        }
}