.title-h1{
        font-size: vw(28);
        font-weight: 700;
        padding-top: vw(25);
        margin-top: 0;
        padding-bottom: vw(20);
        border-bottom: 1px solid #DBE4EB;
        margin-bottom: vw(20);
        @include desktop-xl {
                font-size: px(28);
                padding-top: px(25);
                padding-bottom: px(20);
                margin-bottom: px(20);
        }
        @include devices {
                font-size: vm(24);
                padding-top: vm(15);
                padding-bottom: vm(15);
                margin-bottom: vm(10);
        }
}
.title-h2 {
        font-size: vw(24);
        font-weight: 700;
        display: flex;
        align-items: center;
        margin-top: 0;
        margin-bottom: vw(25);
        svg {
                width: vw(32);
                height: vw(32);
                margin-right: vw(8);
                color: $color-brown;
                @include desktop-xl {
                        width: px(32);
                        height: px(32);
                        margin-right: px(8);
                }
                @include devices {
                        width: vm(28);
                        height: vm(28);
                        margin-right: vm(8);
                }
        }
        @include desktop-xl {
                font-size: px(24);
                margin-bottom: px(25);      
        }
        @include devices {
                font-size: vm(20);
                margin-bottom: vm(18);       
        }
}

.typography {
        p {
                font-size: vw(18);
                line-height: vw(26);
                margin-bottom: vm(20);
                @include desktop-xl {
                        font-size: px(18);
                        line-height: px(26);
                        margin-bottom: px(20);
                }
                @include devices { 
                        font-size: vm(15);
                        line-height: vm(22);
                        margin-bottom: vm(10);
                }
        }
        a {
                color: $color-indigo;
                &:hover {
                        color: $color-brown;
                }
        }
        h3 {
                font-weight: 700;
                font-size: vw(18);
                @include desktop-xl {
                        font-size: px(18);
                }
                @include devices {
                        font-size: vm(18);
                }
        }
        ul {
                li {
                        margin-bottom: vw(15);
                        padding-left: vw(25);
                        position: relative;
                        font-size: vw(18);
                        line-height: vw(26);
                        &:before {
                                content: "-";
                                position: absolute;
                                left: 0;
                                top: 0;
                        }
                        @include desktop-xl {
                                margin-bottom: px(15);
                                padding-left: px(25);
                                font-size: px(18);
                                line-height: px(26);
                        }
                        @include devices {
                                margin-bottom: vm(15);
                                padding-left: vm(25);
                                font-size: vm(15);
                                line-height: vm(22);
                        }
                }
        }
        ol {
                padding-left: 0;
                counter-reset: counter;
                li {
                        margin-bottom: vw(15);
                        padding-left: vw(25);
                        position: relative;
                        font-size: vw(18);
                        line-height: vw(26);
                        &:before {
                                counter-increment: counter; 
                                content: counter(counter)".";
                                font-weight: 500;
                                position: absolute;
                                left: 0;
                                top: 0;
                        }
                        @include desktop-xl {
                                margin-bottom: px(15);
                                padding-left: px(25);
                                font-size: px(18);
                                line-height: px(26);
                        }
                        @include devices {
                                margin-bottom: vm(15);
                                padding-left: vm(25);
                                font-size: vm(15);
                                line-height: vm(22);
                        }  
                }
        }
}

