.pagination {
        display: flex;
        justify-content: space-between;
        align-items: center;
        &__left {
                display: inline-flex;
                align-items: center;
                @include devices {
                        width: 100%;
                        justify-content: space-between;
                }
        }
        &__input {
                width: vw(42);
                height: vw(36);
                border-radius: vw(3);
                text-align: center;
                border: 1.5px solid #C4D1D9;
                transition: all .3s;
                font-size: vw(13);
                &:focus {
                        border: 1px solid $color-indigo;
                }
                @include desktop-xl {
                        width: px(42);
                        height: px(36);
                        border-radius: px(3);
                        font-size: px(13);
                }
                @include devices {
                        width: vm(42);
                        height: vm(36);
                        border-radius: vm(3);
                        font-size: vm(13); 
                }
        }
        &__from {
                font-size: vw(13);
                white-space: nowrap;
                margin: 0 vw(10);
                @include desktop-xl {
                        font-size: px(13);
                        margin: 0 px(10);  
                }
                @include devices {
                        font-size: vm(13);
                        margin: 0 vm(10);  
                }
        }
        &__btns {
                display: flex;
        }
        &__btn {
                width: vw(36);
                height: vw(36);
                background-color: #F0F4F6;
                border: none;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                &--prev {
                        border-radius: vw(6) 0 0 vw(6);
                        @include desktop-xl {
                                border-radius: px(6) 0 0 px(6);
                        }
                        @include devices {
                                border-radius: vm(6) 0 0 vm(6);
                        }
                }
                &--next {
                        background-color: $color-brown;
                        border-radius: 0 vw(6) vw(6) 0;
                        @include desktop-xl {
                                border-radius: 0 px(6) px(6) 0;
                        }
                        @include devices {
                                border-radius: 0 vm(6) vm(6) 0;
                        }
                }
                svg {
                        width: vw(14);
                        height: vw(14);
                        @include desktop-xl {
                                width: px(14);
                                height: px(14);
                        }
                        @include devices {
                                width: vm(14);
                                height: vm(14);
                        }
                }
                &:hover {
                        background-color: $color-indigo;
                }
                @include desktop-xl {
                        width: px(36);
                        height:px(36);
                }
                @include devices {
                        width: vm(36);
                        height:vm(36);
                }
        }      
        &__right {
                font-size: vw(12);
                display: inline-flex;
                align-items: center;
                @include desktop-xl {
                        font-size: px(12);  
                }
                @include devices {
                        font-size: vm(12);  
                        display: none;
                }
        } 
        &__select {
                width: vw(66);
                height: vw(36);
                font-size: vw(13);
                border-radius: vw(3);
                margin: 0 vw(10);
                border: 1.5px solid #C4D1D9;
                text-indent: vw(5);
                outline: none;
                @include desktop-xl {
                        width: px(66);
                        height: px(36);
                        font-size: px(13);
                        border-radius: px(3);
                        margin: 0 px(10);
                        text-indent: px(5);     
                }
                @include devices {
                        width: vm(66);
                        height: vm(36);
                        font-size: vm(13);
                        border-radius: vm(3);
                        margin: 0 vm(10);
                        text-indent: vm(5); 
                }
        }
        @include devices {
                flex-direction: column;
                align-items: flex-start;
        }
}