.header {
        background-color: $color-indigo;
        color: #fff;
        position: relative;
        z-index: 2;
        &--index {
                .header__logo {
                        width: vw(140);
                        height: vw(140);
                        top: vw(60);
                        @include desktop-xl {
                                width: px(140);
                                height: px(140);
                                top: px(60);
                        }
                        @include devices {
                                height: auto;
                                top: 0;
                                width: vm(140);
                        }
                }
               
        }
        &__logo {
                width: vw(57);
                height: vw(57);
                flex-shrink: 0;
                position: relative;
                img {
                        @include devices {
                                width: vm(44);
                                height: vm(44);
                        }  
                }
                @include desktop-xl {
                        width: px(57);
                        height: px(57);
                }
                @include devices {
                        order: 1;
                        display: inline-flex;
                        align-items: center;
                        height: auto;
                        width: vm(140);
                }
        }
        &__top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                height: vw(64);
                @include desktop-xl {
                        height: px(64);          
                }
                @include devices {
                        height: auto;       
                        flex-wrap: wrap;      
                        padding-top: vm(4);
                        padding-bottom: vm(4);
                }
        }
        &__top-wrapper {
                border-bottom: 1px solid #3B4593;
                position: relative;
                z-index: 2;
        }
        &__wrap {
                display: flex;
                align-items: center;
                @include devices {
                        order: 2;
                }
        }
        &__srch-btn {
                margin-left: vw(20);
                svg {
                        width: vw(24);
                        height: vw(24);
                        @include desktop-xl {
                                width: px(24);
                                height: px(24); 
                        }
                        @include devices {
                                width: vm(24);
                                height: vm(24); 
                        }
                }
                @include desktop-xl {
                        margin-left: px(20);      
                }
                @include devices {
                        margin-left: vm(20);          
                }
        }
        &__btm {
                padding-left: vw(160);
                padding-top: vw(20);
                display: flex;
                align-items: center;
                justify-content: space-between;
                @include desktop-xl {
                        padding-left: px(160);
                        padding-top: px(20);        
                }
                @include devices {
                        padding-left: 0;
                        padding-top: vm(16);     
                        flex-direction: column;
                        align-items: flex-start;    
                }
        }
        &__name {
                font-size: vw(14);
                color: #D4D6E2;
                p {
                        margin-bottom: vw(7);
                        &:last-child {
                                margin-bottom: 0;
                        }
                        @include desktop-xl {
                                margin-bottom: px(7);
                        }
                        @include devices {
                                margin-bottom: vm(7);
                        }
                }
                @include desktop-xl {
                        font-size: px(14);        
                }
                @include devices {
                        font-size: vm(12);      
                        margin-bottom: vm(18);     
                }
        }
        &__links {
                display: flex;
        }
        &__link {
                display: flex;
                align-items: center;
                background-color: $color-indigo-dark;
                padding: vw(20);
                height: vw(70);
                border-radius: vw(10);
                margin-left: vw(13);
                font-size: vw(14);
                font-weight: 500;
                &--add {
                        width: vw(245);
                        background: url(../images/bg-add.png) no-repeat center center;
                        background-size: cover;
                        @include desktop-xl {
                                width: px(245);
                        }
                        @include devices {
                                width: vm(164);
                        }
                }
                &:first-child {
                        margin-left: 0;
                }
                svg {
                        width: vw(26);
                        height: vw(26);
                        color: $color-brown;
                        margin-right: vw(10);
                        @include desktop-xl {
                                width: px(26);
                                height: px(26);
                                margin-right: px(10);  
                        }
                        @include devices {
                                width: vm(22);
                                height: vm(22);
                                margin-right: vm(10);  
                        }
                }
                @include desktop-xl {
                        padding: px(20);
                        height: px(70);
                        border-radius: px(10);
                        margin-left: px(13);
                        font-size: px(14);    
                }
                @include devices {
                        padding: vm(20) vm(16);
                        height: vm(60);
                        border-radius: vm(10);
                        margin-left: vm(13);
                        font-size: vm(12);         
                }
        }

        &__nav {
                margin-left: vw(-24);
                @include desktop-xl {
                        margin-left: px(-24);
                }
                @include devices {
                        margin-left: 0;      
                        order: 3;
                        flex-basis: 100%;
                        width: 100%;
                        padding-bottom: vm(40);
                        display: none;
                }
        }

        &__lng {
                @include devices {
                       display: none !important;
                }
        }
}

.nav {
        display: flex;
        align-items: center;
        font-size: 700;
        font-size: vw(16);
        z-index: 10;
        position: relative;
        li {
                margin-right: vw(40);
                position: relative;
                height: vw(65);
                display: inline-flex;
                align-items: center;
                &:hover {
                        ul {
                                display: block;
                        }
                }
                ul {
                        position: absolute;
                        left: 0;
                        top: 100%;
                        background-color: #fff;
                        width: vw(210);
                        border-radius: vw(10);
                        padding: vw(10) 0;
                        box-shadow: 0 vw(5) vw(45) rgba($color-indigo, .1);
                        display: none;
                        li {
                                height: auto;
                                a {
                                        color: $color-black;
                                        font-size: vw(14);
                                        line-height: vw(17);
                                        padding: vw(7) vw(13);
                                        display: flex;
                                        @include desktop-xl {
                                                font-size: px(14);
                                                line-height: px(17);
                                                padding: px(7) px(13);
                                        }
                                        @include devices {
                                                font-size: vm(14);
                                                line-height: vm(17);
                                                padding: vm(7) vm(13);
                                        }
                                }
                        }
                        @include desktop-xl {
                                width: px(210);
                                border-radius: px(10);
                                padding: px(10) 0;
                                box-shadow: 0 px(5) px(45) rgba($color-indigo, .1);
                        }
                        @include devices {
                                width: vm(210);
                                border-radius:vmx(10);
                                padding: vm(10) 0;
                                box-shadow: 0 vm(5) vm(45) rgba($color-indigo, .1);
                                display: none;
                        }
                }
                @include desktop-xl {
                        margin-right: px(40);       
                        height: px(65); 
                }
                @include devices {
                        margin-right: 0;       
                        width: 100%;  
                        height: auto;
                }
                
        }
        a {
                display: flex;
                width: 100%;
                &:hover {
                        color: $color-brown-light;
                }
                @include devices {
                        padding: vm(18) vm(15);
                        border-radius: vm(10);
                        padding-left: vm(8);
                }
        }
        @include desktop-xl {
                font-size: px(16);              
        }
        @include devices {
                font-size: vm(14);
                font-weight: 700;     
                flex-direction: column;             
        }
}

.lng {
        display: flex;
        font-size: vw(14);
        font-weight: 500;
        &--mobile {
                margin-top: vm(40);
                a, span {
                        @include devices {
                                margin-left: 0;     
                                margin-right: vm(8);       
                        }
                }
        }
        a, span {
                margin-left: vw(8);
                color: rgba($color-white, .3);
                @include desktop-xl {
                        margin-left: px(8);        
                }
                @include devices {
                        margin-left: vm(8);            
                }
        }
        a {
                &:hover {
                        color: rgba($color-white, 1) ;
                }
        }
        .is-active {
                color: rgba($color-white, 1) ;
        }
        @include desktop-xl {
                font-size: px(14);                
        }
        @include devices {
                font-size: vm(13);                
        }
}

.btn-mobile-menu {
        @include devices {
                border: none;
                background-color: transparent;
                margin-right: vm(8);
                width: vm(36);
                svg {
                        width: vm(24);
                        height: vm(24);
                }
        }
        &__open {
                display: none;
                svg {
                        width: vm(32);
                        height: vm(32);
                }
        }
        &.is-active {
                .btn-mobile-menu__open {
                        display: block;
                }
                .btn-mobile-menu__close {
                        display: none;
                }
        }
}