// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS
$color-white: #fff;
$color-gray: #6D767E;
$color-black: #191B1D;
$color-brown: #B07333;
$color-brown-light: #FAD1A6;
$color-indigo: #293170;
$color-indigo-dark: #20275F;




// FONTS
// stylelint-disable-next-line
$font-primary: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen, ubuntu, cantarell, "Open Sans", "Helvetica Neue", sans-serif;
$font-inter: "Inter";

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// Settings
$desktop: 1025;
$desktop-xl: 1280;
$laptop: 1440;
$tablet: 992;
$mobile: 320;
$desktop-vh: 750;
