.about-us {
        &__preview {
                margin-bottom: vw(20);
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        margin-bottom: px(20);
                        border-radius: px(10);    
                }
                @include devices { 
                        margin-bottom: vm(20);
                        border-radius: vm(10);   
                }
        }
        @include desktop-xl {
                padding-bottom: px(96);      
        } 
}