.expedition {
        padding-bottom: vw(60);
        padding-top: vw(60);
        position: relative;
        overflow: hidden;
        z-index: 1;
        &:after {
                content: "";
                position: absolute;
                z-index: -1;
                top: 0;
                left: 0;
                right: 0;
                height: vw(240);
                background-color: $color-indigo;
                @include desktop-xl {
                        height: px(260); 
                }
                @include devices {
                        height: vm(100); 
                }
        }
        &__header {
                display: flex;
                justify-content: space-between;
                margin-bottom: vw(25);
                .title-h2 {
                        margin-bottom: 0;
                        color: #fff;
                        svg {
                                color: $color-brown-light;
                        }
                }
                @include desktop-xl {
                        margin-bottom: px(25); 
                }
                @include devices {
                        margin-bottom: vm(22); 
                }
        }
        &__btns {
                display: flex;
        }
        &__btn {
                padding: 0;
                border: none;
                background: none;
                margin-left: vw(4);
                svg {
                        width: vw(30);
                        height: vw(30);
                        color: $color-brown-light;
                        transition: all .3s ease-out;
                        @include desktop-xl {
                                width: px(30);
                                height: px(30);
                        }
                        @include devices {
                                width: vm(26);
                                height: vm(26);
                        }
                }
                &:hover {
                        svg {
                                color: $color-brown;  
                        }
                }
                @include desktop-xl {
                        margin-left: px(4);  
                }
                @include devices {
                        margin-left: vm(4);  
                }
        }
        &__more {
                font-size: vw(14);
                display: inline-flex;
                color: $color-brown;
                @include desktop-xl {
                        font-size: px(14);
                }
                @include devices {
                        font-size: vm(12);
                }
        }
        @include desktop-xl {
                padding-bottom: px(60);
                padding-top: px(60);
        }
        @include devices {
                padding-bottom: vm(42);
                padding-top: vm(20);       
        }
}


.expedition-slider {
        margin-left: vw(-15);
        margin-right: vw(-80);
        &__item {
                padding-left: vw(15);
                padding-right: vw(15);
                @include desktop-xl {
                        padding-left: px(15);
                        padding-right: px(15); 
                }
                @include devices {
                        padding-left: 0;
                        padding-right: vm(14); 
                        width: vm(300) !important;
                }
        }
        @include desktop-xl {
                margin-left: px(-15);
                margin-right: px(-320);         
        }
        @include devices {
                margin-left: 0;
                margin-right: vm(-10);                   
        }
}
.date-pub {
        color: $color-brown;
        font-size: vw(12);
        @include desktop-xl {
                font-size: px(12);
        }
        @include devices {
                font-size: vm(11);       
        }

}
.exp-item {
        &__preview {
                position: relative;
                border-radius: vw(10);
                overflow: hidden;
                margin-bottom: vw(7);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        border-radius: px(10);
                        margin-bottom: px(7);
                }
                @include devices {
                        border-radius: vm(10);
                        margin-bottom: vm(7);
                }
        }
        &__date {
                margin-bottom: vw(12);
                @include desktop-xl {
                        margin-bottom: px(12);
                }
                @include devices {
                        margin-bottom: vm(10);
                }
        }
        &__title {
                font-size: vw(16);
                font-weight: 700;
                margin-bottom: vw(20);
                a {
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(16);
                        margin-bottom: px(20);
                }
                @include devices {
                        font-size: vm(13.5);
                        line-height: vm(17);
                        margin-bottom: vm(12);
                }
        }
}

.events-block {
        margin-bottom: vw(60);
        &__container {
                display: flex;
                @include devices {
                        flex-direction: column;
                }
        }
        &__events {
                flex: 1 1;
                margin-right: vw(20);
                @include desktop-xl {
                        margin-right: px(20);
                }
                @include devices {
                        margin-right: 0;
                        margin-bottom: vm(30);
                }
        }
        &__calendar {
                flex-basis: vw(340);
                flex-shrink: 0;
                background-color: #f7f7f7;
                @include desktop-xl {
                        flex-basis: px(340); 
                }
                @include devices {
                        flex-basis: vm(290); 
                }
        }
        @include desktop-xl {
                margin-bottom: px(60);   
        }
        @include devices {
                margin-bottom: vm(40);         
        }
}

.event {
        display: flex;
        margin-bottom: vw(23);
        &--employees {
                flex-direction: column;
                .event__image {
                        width: 100%;
                        margin-right: 0;
                        margin-bottom: vw(20);
                        @include desktop-xl {
                                margin-bottom: px(20);
                        }
                        @include devices {
                                margin-bottom: vm(20);
                        }
                }
        }
        &:last-child {
                margin-bottom: 0;
        }
        &__image {
                width: vw(200);
                flex-shrink: 0;
                margin-right: vw(32);
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        width: px(200);
                        margin-right: px(32);  
                        border-radius: px(10);
                }
                @include devices {
                        width: 100%;
                        margin-right: 0;  
                        border-radius: vm(10);
                        margin-bottom: vm(18);
                }
        }
        &__date {
                margin-bottom: vw(15);
                font-size: vw(14);
                @include desktop-xl {
                        margin-bottom: px(15);
                        font-size: px(14);
                }
                @include devices {
                        margin-bottom: vm(6);
                        font-size: vm(11);
                }
        }
        &__title {
                font-weight: 700;
                font-size: vw(18);
                line-height: vw(23);
                margin-bottom: vw(12);
                a {
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(18);
                        line-height: px(23);
                        margin-bottom: px(12); 
                }
                @include devices {
                        font-size: vm(15.5);
                        line-height: vm(19.5);
                        margin-bottom: vm(10); 
                }
        }
        &__intro {
                font-size: vw(14);
                line-height: vw(17);
                font-weight: 500;
                color: $color-gray;
                @include desktop-xl {
                        font-size: px(14); 
                        line-height: px(17);
                }
                @include devices {
                        font-size: vm(12); 
                        line-height: vm(14);
                }
        }
        @include desktop-xl {
                margin-bottom: px(23);          
        }
        @include devices {
                margin-bottom: vm(18);      
                flex-direction: column;          
        }
}

.news-block {
        padding-bottom: vw(60);
        &__container  {
                display: flex;
                @include devices {
                        flex-direction: column;
                }
        }
        &__main {
                flex-basis: 40.3%;
                @include devices {
                        flex-basis: 100%;
                }
        }
        &__other {
                flex-basis: 59.7%;
                display: flex;
                flex-wrap: wrap;
                padding-left: vw(31);
                @include desktop-xl {
                        padding-left: px(31);
                }
                @include devices {
                        padding-left: 0;
                        flex-direction: column;
                        flex-basis: 100%;
                }
        }
        &__more {
                font-size: vw(14);
                display: inline-flex;
                color: $color-brown;
                margin-top: vw(20);
                @include desktop-xl {
                        font-size: px(14);
                        margin-top: px(20);
                }
                @include devices {
                        font-size: vm(12);
                        margin-top: vm(20);
                        display: none;
                }
        }
        @include desktop-xl {
                padding-bottom: px(60);
        }
        @include devices {
                padding-bottom: vm(44);     
        }

}

.news-item {
        flex-basis: calc(50% - #{vw(26)});
        margin-left: vw(13);
        margin-right: vw(13);
        padding-left: vw(25);
        padding-top: vw(18);
        padding-right: vw(11);
        padding-bottom: vw(20);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        border-bottom: 1px solid #D9E2E9;
        &--employee {
                padding-left: 0 !important;
                padding-right: 0 !important;
                padding-top: 0 !important;
                margin-left: 0 !important;
                margin-right: 0 !important;
                margin-bottom: vw(10);
                @include desktop-xl {
                        margin-bottom: px(10);
                }
                @include devices {
                        margin-bottom: vm(10);
                }
        }
        &--newspage  {
                .news-item__title {
                        font-size: vw(18);
                        @include desktop-xl {
                                font-size: px(18);
                        }
                        @include devices {
                                font-size: vm(14);
                        }
                }
        }
        &__title {
                font-size: vw(16);
                font-weight: 700;
                min-height: vw(75);
                display: flex;
                align-items: center;
                &:hover {
                        color: $color-brown;
                }
                @include desktop-xl {
                        font-size: px(16);
                        min-height: px(75);
                }
                @include devices {
                        font-size: vm(14);
                        min-height: vm(75);
                }
        }
        @include desktop-xl {
                flex-basis: calc(50% - #{px(26)});
                margin-left: px(13);
                margin-right: px(13);
                padding-left: px(25);
                padding-top: px(18);
                padding-right: px(11);
                padding-bottom: px(20);        
        }
        @include devices {
                flex-basis: calc(50% - #{vm(26)});
                margin-left: vm(13);
                margin-right: vm(13);
                padding-left: vm(8);
                padding-top: vm(14);
                padding-right: vm(11);
                padding-bottom: vm(14);     
        }
}

.news-main {
        position: relative;
        z-index: 1;
        overflow: hidden;
        border-radius: vw(10);
        &:after {
                content: "";
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                z-index: 1;
                background: linear-gradient(180.59deg, rgba(0, 0, 0, 0) 8.71%, rgba(0, 0, 0, 0.811804) 78.27%, #000000 99.49%);
        }
        a {
                display: flex;
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                z-index: 1;
                &:hover {
                        color: $color-brown-light;
                }
                @include desktop-xl {
                        border-radius: px(10);  
                }
                @include devices {
                        border-radius: vm(10);  
                }
        }
        &__inf {
                z-index: 2;
                display: flex;
                flex-direction: column;
                position: absolute;
                padding: vw(33) vw(30);
                bottom: 0;
                left: 0;
                right: 0;
                color: #fff;
                @include desktop-xl {
                        padding: px(33) px(30);
                }
                @include devices {
                        padding: vm(33) vm(23);
                }
        }
        &__title {
                font-size: vw(24);
                font-weight: 700;
                margin-bottom: vw(24);
                @include desktop-xl {
                        font-size: px(24);
                        margin-bottom: px(24);
                }
                @include devices {
                        font-size: vm(15.5);
                        line-height: vm(19);
                        margin-bottom: vm(6);
                }
        }
        &__date {
                font-size: vw(13);
                color: #B49E84;
                @include desktop-xl {
                        font-size: px(13);
                }
                @include devices {
                        font-size: vm(11);
                }
        }
        @include desktop-xl {
                border-radius: px(10);   
        }
        @include devices {
                border-radius: vm(10);         
        }
}

.tab-wrapper {
        .tabs {
                display: flex;
                margin-bottom: vw(20);
                border-bottom: 1px solid #DBE4EB;
                @include desktop-xl {
                        margin-bottom: px(20);
                }
                @include devices {
                        margin-bottom: vm(20);
                        overflow-x: scroll;
                }
        }
        .tab {
                display: inline-flex;
                justify-content: center;
                text-align: center;
                color: $color-gray;
                font-size: vw(14);
                font-weight: 500;
                padding: vw(10) vw(28);
                min-width: vw(132);
                position: relative;
                cursor: pointer;
                &:after {
                        content: "";
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        right: 0;
                        height: vw(2);
                        background-color: $color-brown;
                        visibility: hidden;
                        opacity: 0;
                        transition: all .3s;
                        @include desktop-xl {
                                height: px(2);
                        }
                        @include devices {
                                height: vm(2);
                        }
                }
                &.active {
                        color: $color-black;
                        &:after {
                                visibility: visible;
                                opacity: 1;
                        }
                }
                @include desktop-xl {
                        font-size: px(14);
                        padding: px(10) px(28);
                        min-width: px(132);
                }
                @include devices {
                        font-size: vm(12);
                        padding: vm(10) vm(10);
                        min-width: vm(140);
                        white-space: nowrap;
                }
        }
}

.publications {
        padding-bottom: vw(60);
       

        &__more {
                font-size: vw(14);
                display: inline-flex;
                color: $color-brown;
                margin-top: vw(20);
                @include desktop-xl {
                        font-size: px(14);
                        margin-top: px(20);
                }
                @include devices {
