                }
        }
        @include desktop-xl {
                padding-bottom: px(60);
        }
        @include devices {
                padding-bottom: vm(50);      
        }
}
.publications-slider  {
        margin-left: vw(-15);
        margin-right: vw(-15);
        &__item {
                padding-left: vw(15);
                padding-right: vw(15);
                @include desktop-xl {
                        padding-left: px(15);
                        padding-right: px(15);
                }
                @include devices {
                        padding-left: 0;
                        padding-right: vm(14); 
