                }
        }
        @include desktop-xl {
                margin-left: px(-15);
                margin-right: px(-15);
        }
        @include devices {
                margin-left: 0;
                margin-right: vm(-10);     
        }
}

.publication {
        &__readmore {
                font-size: vw(13);
                position: absolute;
                bottom: vw(24);
                left: 50%;
                transform: translate(-50%, 0);
                color: #fff;
                background-color: $color-indigo;
                border-radius: vw(10);
                display: inline-flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                height: vw(42);
                padding-left: vw(45);
                padding-right: vw(45);
                visibility: hidden;
                opacity: 0;
                transition: all .3s ease-out;
                @include desktop-xl {
                        font-size: px(13);
                        bottom: px(24);
                        border-radius: px(10);
                        height: px(42);
                        padding-left: px(45);
                        padding-right: px(45);
                }
        }
        &:hover {
                .publication__readmore {
                        visibility: visible;
                        opacity: 1;
                }
        }
        &__preview  {
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                margin-bottom: vw(20);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        border-radius: px(10);
                        margin-bottom: px(20);
                }
                @include devices {
                        border-radius: vm(10);
                        margin-bottom: vm(14);
                }
        }
        &__title  {
                font-size: vw(14);
                line-height: vw(17);
                font-weight: 700;
                a {
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(14);
                        line-height: px(17);
                }
                @include devices {
                        font-size: vm(12);
                        line-height: vm(17);
                }
        }
       
}

