.filter {
        display: flex;
        justify-content: space-between;
        margin-bottom: vw(20);
        &__item {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                width: vw(22);
                height: vw(20);
                font-size: vw(16);
                font-weight: 500;
                color: #6D767E;
                border-radius: vw(3);
                &:hover {
                        color: $color-indigo;
                }
                &.is-active {
                        color: #fff;
                        background-color: $color-indigo;
                }
                @include desktop-xl {
                        width: px(22);
                        height: px(20);
                        font-size: px(16);
                        border-radius: px(3);
                }
                @include devices {
                        width: vm(20);
                        height: vm(20);
                        font-size: vm(16);
                        border-radius: vm(3);
                }
        }
        @include desktop-xl {
                margin-bottom: px(20);     
        }
        @include devices {
                margin-bottom: vm(20);    
                flex-wrap: wrap;        
        }
}

.employees {
        &__search {
                display: flex;
                position: relative;
                margin-bottom: vw(16);
                &-input {
                        height: vw(42);
                        text-indent: vw(14);
                        border-radius: vw(6);
                        font-size: vw(14);
                        border: 1px solid #DBE4EB;
                        width: 100%;
                        transition: all .3s;
                        &:focus {
                                border: 1px solid $color-indigo;
                        }
                        @include desktop-xl {
                                height: px(42);
                                text-indent: px(14);
                                border-radius: px(6);
                                font-size: px(14);
                        }
                        @include devices {
                                height: vm(42);
                                text-indent: vm(14);
                                border-radius: vm(6);
                                font-size: vm(14);
                        }
                }
                &-btn {
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: vw(42);
                        height: vw(42);
                        border: none;
                        background-color: transparent;
                        color: #3A3D40;
                        @include desktop-xl {
                                width: px(42);
                                height: px(42);
                        }
                        @include devices {
                                width: vm(42);
                                height: vm(42);
                        }
                }
                @include desktop-xl {
                        margin-bottom: px(16);
                }
                @include devices {
                        margin-bottom: vm(16); 
                }
        }
        &__notfound {
                text-align: center;
                padding-top: vw(100);
                padding-bottom: vw(100);
                font-size: vw(13);
                color: #A7B1B7;
                @include desktop-xl {
                        padding-top: px(100);
                        padding-bottom: px(100);
                        font-size: px(13);
                }
                @include devices {
                        padding-top: vm(100);
                        padding-bottom: vm(100);
                        font-size: vm(13);
                }
        }
}
.employees-item  {
        border-radius: vw(10);
        margin-bottom: vw(10);
        border: 1px solid #DBE4EB;
        padding: vw(14);
        display: flex;
        transition: all .3s ease-out;
        position: relative;
        &:after {
                content: "";
                position: absolute;
                display: inline-block;
                top: 50%;
                right: vw(18);
                width: vw(24);
                height: vw(24);
                transform: translate(0, -50%);
                background: url(../images/sprites/svg/arrow-blue.svg) no-repeat center center;
                visibility: hidden;
                opacity: 0;
                transition: all .3s ease-out;
        }
        &__image {
                margin-right: vw(25);
                width: vw(74);
                height: vw(83);
                position: relative;
                overflow: hidden;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                flex-shrink: 0;
                @include desktop-xl {
                        margin-right: px(25);
                        width: px(74);
                        height: px(83);
                }
                @include devices {
                        margin-right: vm(25);
                        width: vm(74);
                        height: vm(83);
                }
        }
        &__title {
                font-size: vw(16);
                font-weight: 700;
                margin-bottom: vw(7);
                a {
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(16);
                        margin-bottom: px(7); 
                }
                @include devices {
                        font-size: vm(16);
                        margin-bottom: vm(7); 
                }
        }
        &__subtitle {
                color: #6D767E;
                font-size: vw(14);
                @include desktop-xl {
                        font-size: px(14); 
                }
                @include devices {
                        font-size: vm(14); 
                }
        }
        &:hover {
                border: 1px solid $color-brown;
                &:after {
                        content: "";
                        visibility: visible;
                        opacity: 1;
                }
        }
        @include desktop-xl {
                border-radius: px(10);
                margin-bottom: px(10);
                padding: px(14)          
        }
        @include devices {
                border-radius: vm(10);
                margin-bottom: vm(10);
                padding: vm(14)           
        }
}
