.page {
        padding-bottom: vw(96);
        &__container {
                display: flex;
                @include devices { 
                        flex-direction: column;
                }
        }
        &__main  {
                flex: 1 1;
                margin-right: vw(150);
                @include desktop-xl {
                        margin-right: px(150);
                }
                @include devices { 
                        margin-right: 0;
                }
        }
        &__aside {
                flex-basis: vw(330);
                padding-top: vw(80);
                @include desktop-xl {
                        flex-basis: px(330);
                        padding-top: px(80); 
                }
                @include devices { 
                        flex-basis: 100%;
                        padding-top: vm(40);
                }
        }
        @include devices { 
                padding-bottom: vm(40);             
        }
}