.news-full {
        &__date {
                display: flex;
                font-size: vw(13);
                color: $color-brown;
                font-weight: 400;
                margin-top: vw(20);
                @include desktop-xl {
                        font-size: px(13);
                        margin-top: px(20);
                }
                @include devices { 
                        font-size: vm(13);
                        margin-top: vm(20);
                }
        }
        &__titlecat {
                margin-left: vw(20);
                padding-left: vw(20);
                border-left: 1px solid $color-indigo;
                a {
                        color: $color-indigo;
                        &:hover {
                                color: $color-brown; 
                        }
                }
                @include desktop-xl {
                        margin-left: px(20);
                        padding-left: px(20);
                }
                @include devices { 
                        margin-left: vm(20);
                        padding-left: vm(20);
                }
        }
        &__preview {
                margin-bottom: vw(20);
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        margin-bottom: px(20);
                        border-radius: px(10);    
                }
                @include devices { 
                        margin-bottom: vm(20);
                        border-radius: vm(10);   
                }
        }
}