.footer {
        background-color: #DFA05E;
        padding-top: vw(33);
        color: #fff;
        &__logo {
                display: flex;
                align-items: center;
                &-image {
                        width: vw(70);
                        height: vw(70);
                        margin-right: vw(30);
                        @include desktop-xl {
                                width: px(70);
                                height: px(70);
                                margin-right: px(30);
                        }
                        @include devices {
                                width: vm(70);
                                height: vm(70);
                                margin-right: vm(30);
                        }
                }
                &-text {
                        font-size: vw(14);
                        color: rgba(255,255,255,.8);
                        p {
                                margin-bottom: vw(7);
                                &:last-child {
                                        margin-bottom: 0;
                                }
                                @include desktop-xl {
                                        margin-bottom: px(7);
                                }
                                @include devices {
                                        margin-bottom: vm(7);
                                }
                        }
                        @include desktop-xl {
                                font-size: px(14);
                        }
                        @include devices {
                                font-size: vm(12);
                        }
                }
                @include devices {
                        flex-direction: column;
                        align-items: flex-start;
                }
        }
        &__middle {
                padding-top: vw(40);
                padding-bottom: vw(20);
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-bottom: 1px solid rgba(255,255,255,.4);
                @include desktop-xl {
                        padding-top: px(40);
                        padding-bottom: px(20);         
                }
                @include devices {
                        padding-top: vm(20);
                        padding-bottom: vm(24);            
                }
        }
        &__nav {
                li {
                        margin-left: 0;
                        margin-right: vw(44);
                        @include desktop-xl {
                                margin-right: px(44); 
                        }
                        @include devices {
                                margin-right: vm(44); 
                        }
                }
                @include devices {
                        display: none;
                }
        }
        &__btm {
                padding-top: vw(30);
                padding-bottom: vw(50);
                display: flex;
                align-items: flex-end;
                justify-content: space-between;
                @include desktop-xl {
                        padding-top: px(30);
                        padding-bottom: px(50);           
                }
                @include devices {
                        padding-top: vm(30);
                        padding-bottom: vm(18);           
                        flex-direction: column; 
                        align-items: flex-start;
                }
        }
        &__contacts {
                display: flex;
                @include devices {
                        flex-direction: column;
                }
        }
        &__contact {
                margin-right: vw(90);
                &:last-child {
                        margin-right: 0;
                }
                @include desktop-xl {
                        margin-right: px(90);      
                }
                @include devices {
                        margin-right: 0;     
                        margin-bottom: vm(16);       
                }
        }
        &__copyright {
                font-size: vw(13.5);
                @include desktop-xl {
                        font-size: px(13.5);          
                }
                @include devices {
                        font-size: vm(11.5);      
                        margin-top: vm(20);     
                }
        }
        @include desktop-xl {
                padding-top: px(33);               
        }
        @include devices {
                padding-top: vm(28);                   
        }
}

.social  {
        display: flex;
        a {
                margin-left: vw(20);
                &:hover {
                        color: $color-brown-light;
                }
                @include desktop-xl {
                        margin-left: px(20);           
                }
                @include devices {
                        margin-left: 0; 
                        margin-right: vm(20);          
                }
        }
        svg {
                width: vw(20);
                height: vw(20);
                @include desktop-xl {
                        width: px(20);
                        height: px(20);          
                }
                @include devices {
                        width: vm(20);
                        height: vm(20);        
                }
        }
}

.contact {
        display: flex;
        align-items: center;
        &__ico {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                width: vw(50);
                height: vw(50);
                border-radius: 50%;
                background-color: #E1E5EB;
                margin-right: vw(20);
                svg {
                        width: vw(24);
                        height: vw(24);
                        color: $color-black;
                        @include desktop-xl {
                                width: px(24);
                                height: px(24);
                        }
                        @include devices {
                                width: vm(24);
                                height: vm(24);
                        }
                }
                @include desktop-xl {
                        width: px(50);
                        height: px(50);
                        margin-right: px(20);       
                }
                @include devices {
                        width: vm(42);
                        height: vm(42);
                        margin-right: vm(20);           
                }
        }
        &__subtitle {
                font-size: vw(14);
                font-weight: 700;
                margin-bottom: vw(6);
                @include desktop-xl {
                        font-size: px(14);    
                        margin-bottom: px(6);     
                }
                @include devices {
                        font-size: vm(12);    
                        margin-bottom: vm(6);           
                }
        }
        &__title {
                font-family: "Intro";
                font-size: vw(24);
                @include desktop-xl {
                        font-size: px(24);           
                }
                @include devices {
                        font-size: vm(20);          
                }
        }
        a {
                &:hover {
                        color: $color-brown-light;
                }
        }
}