.employee {
        &__header {
                display: flex;
                margin-top: vw(24);
                margin-bottom: vw(18);
                @include desktop-xl {
                        margin-top: px(24);
                        margin-bottom: px(18);
                }
                @include devices {
                        margin-top: vm(24);
                        margin-bottom: vm(18);
                        flex-direction: column;
                        align-items: flex-start;
                }
        }
        &__avatar{
                position: relative;
                overflow: hidden;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                flex-shrink: 0;
                border-radius: vw(10);
                margin-right: vw(22);
                @include desktop-xl {
                        border-radius: px(10);
                        margin-right: px(22);  
                }
                @include devices {
                        border-radius: vm(10);
                        margin-right: 0;  
                        margin-bottom: vm(20);
                }
        }
        &__description {
                flex: 1 1;
        }
        &__title {
                font-size: vw(28);
                font-weight: 700;
                margin-top: 0;
                margin-bottom: vw(15);
                @include desktop-xl {
                        font-size: px(28);
                        margin-bottom: px(15);  
                }
                @include devices {
                        font-size: vm(22);
                        margin-bottom: vm(15);  
                }
        }
        &__position {
                font-size: vw(14);
                margin-bottom: vw(5);
                @include desktop-xl {
                        font-size: px(14);
                        margin-bottom: px(5);
                }
                @include devices {
                        font-size: vm(14);
                        margin-bottom: vm(5);
                }
        }
        &__email {
                font-size: vw(14);
                a {
                        color: $color-indigo;
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(14);
                }
                @include devices {
                        font-size: vm(14);
                }
        }
}

.documents {
        display: flex;
        flex-wrap: wrap;
        margin-left: vw(-12);
        margin-right: vw(-12);
        @include desktop-xl {
                margin-left: px(-12);
                margin-right: px(-12);          
        }
        @include devices {
                margin-left: vm(-6);
                margin-right: vm(-6);            
        }
}
.doc {
        margin-left: vw(12);
        margin-right: vw(12);
        flex-basis: calc(25% - #{vw(24)});
        margin-bottom: vw(24);
        &__image {
                position: relative;
                overflow: hidden;
                border-radius: vw(10);
                margin-bottom: vw(10);
                img {
                        width: 100%;
                }
                @include desktop-xl {
                        border-radius: px(10);
                        margin-bottom: px(10);
                }
                @include devices {
                        border-radius: vm(10);
                        margin-bottom: vm(10);
                }
        }
        &__title {
                font-size: vw(14);
                font-weight: 700;
                a {
                        &:hover {
                                color: $color-brown;
                        }
                }
                @include desktop-xl {
                        font-size: px(14); 
                }
                @include devices {
                        font-size: vm(14); 
                }
        }
        @include desktop-xl {
                margin-left: px(12);
                margin-right: px(12);
                flex-basis: calc(25% - #{px(24)});
                margin-bottom: px(24);       
        }
        @include devices {
                margin-left: vm(6);
                margin-right: vm(6);
                flex-basis: calc(50% - #{vm(12)});
                margin-bottom: vm(24);          
        }
}